import React, { PureComponent } from 'react';
//import { Test } from './SearchBar.styles';
import { Navbar, Icon, NavItem } from 'react-materialize';

class SearchBar extends PureComponent { 
  constructor(props) {
    super(props);

    this.state = {
      hasError: false,
    };
  }

  componentDidMount = () => {
    console.log('SearchBar mounted');
  }

  static getDerivedStateFromError(error) {
    // getDerivedStateFromError -> Update state so the next render will show the fallback UI.
    return { hasError: true };
  }

  componentDidCatch(error, info) {
    // You can also log the error to an error reporting service
  }

  getDerivedStateFromProps = (nextProps, prevState) => {
    console.log('SearchBar getDerivedStateFromProps', nextProps, prevState);
  }

  getSnapshotBeforeUpdate = (prevProps, prevState) => {
    console.log('SearchBar getSnapshotBeforeUpdate', prevProps, prevState);
  }

  componentDidUpdate = () => {
    console.log('SearchBar did update');
  }

  componentWillUnmount = () => {
    console.log('SearchBar will unmount');
  }

  render () {
    if (this.state.hasError) {
      return <h1>Something went wrong.</h1>;
    }
    return (
      <div className="SearchBarWrapper">
        <Navbar
          alignLinks="right"
          id="mobile-nav"
          menuIcon={<Icon>menu</Icon>}
          options={{
            draggable: true,
            edge: 'left',
            inDuration: 250,
            onCloseEnd: null,
            onCloseStart: null,
            onOpenEnd: null,
            onOpenStart: null,
            outDuration: 200,
            preventScrolling: true
          }}
          className="teal darken-3 search-input"
          search
        >
          <NavItem href="">
    Getting started
  </NavItem>
  <NavItem href="components.html">
    Components
  </NavItem>
        </Navbar>
      </div>
    );
  }
}

SearchBar.propTypes = {
  // bla: PropTypes.string,
};

SearchBar.defaultProps = {
  // bla: 'test',
};

export default SearchBar;
