import React from 'react';
import { NavLink } from 'react-router-dom';
import { Chip, Icon, Button } from 'react-materialize'

export default function ArticlesSliderElement(props) {
  const article = props.article
  console.log(article.img)
  return (
  <NavLink className="carousel-item white-text" style={{backgroundImage: 'url('+ article.img +')'}} to={'/article/' + article.href}>
      <h3 className="carousel-heading">{article.name}</h3>
      <p className='article-text'>{article.pre_text}</p>
      <div className="carousel-footer">
        <Chip
            close={false}
            closeIcon={<Icon className="close">close</Icon>}
            options={null}
            className="green lighten-3 date-chip"
          >
          <Icon className='edit'></Icon>
          <span>{article.date}</span>
          
        </Chip>
        {article.isNew ? <Chip
            close={false}
            closeIcon={<Icon className="close">close</Icon>}
            options={null}
            className="green accent-2 date-chip"
            style={{marginLeft: '5px', marginRight: '5px'}}
          >
          <Icon className='edit'></Icon>
          <span>Новое</span>
        </Chip> : null}
        
        <Button flat node="button" waves='teal' className='white-text transparent btns'>Читать</Button>
      </div>
    </NavLink>
  )
}
