import React, { useEffect } from 'react';
import M from 'materialize-css'
import ArticlesSliderElement from './ArticlesSliderElement/ArticlesSliderElement';

export default function ArticlesSlider(props) {
  useEffect(initCarousel, [])

  return (
    <div className="main-page__carousel-wrapper">
      <div
        id="carousel-main"
        carouselId="Carousel-2"
        className="white-text center carousel carousel-slider">
        {props.ArticlesList.map((item, index) =>
          <ArticlesSliderElement key={index} article={item}/>
        )}
      </div>

    </div>
  )
}


function onWheel(instance) {
  return function(e) {
    e = e || window.event;

  // wheelDelta не даёт возможность узнать количество пикселей
    var delta_ = e.deltaY || e.detail || e.wheelDelta;
    
    if (delta_ > 0) {
      instance.next()
    } else {
      instance.prev()
    }
    e.preventDefault ? e.preventDefault() : (e.returnValue = false);

  }
}

function initCarousel() {
  const elem = document.getElementById('carousel-main')
    const instanceCarousel = M.Carousel.init(elem, {
      carouselId: "Carousel-2",
      duration: 70,
      fullWidth: true,
      indicators: true,
      noWrap: false,
    })
    if (elem.addEventListener) {
      if ('onwheel' in document) {
        // IE9+, FF17+, Ch31+
        elem.addEventListener("wheel", onWheel(instanceCarousel));
      } else if ('onmousewheel' in document) {
        // устаревший вариант события
        elem.addEventListener("mousewheel", onWheel(instanceCarousel));
      } else {
        // Firefox < 17
        elem.addEventListener("MozMousePixelScroll", onWheel(instanceCarousel));
      }
    } else { // IE8-
      elem.attachEvent("onmousewheel", onWheel(instanceCarousel));
    }
}