import React from 'react';
import { Collapsible, CollapsibleItem, Icon, } from 'react-materialize';

export default function ArticleContentList(props) {
  if (!props.titles)
    props.titles = [[]]

  return (
    <Collapsible accordion className='article-filter article-content-list teal'>
      <CollapsibleItem
        expanded={false}
        header={
          <h6 id='article-content-list' className='article-subtitle'>Содержание</h6>
        }
        node="div"
      >
      <div className='article-filter_title'>
        <Icon>list</Icon>
        <p>{props.name || 'Мотоцикл Урал'}</p>
      </div>
      <div className="article-content-list-block">
        {props.titles.map((item, index) => {
          const title = <a href={`#${item[0]}`} className="article-content-list__title">{item[0]}</a>
          const items = [title, ...item[1].map((sitem, index_) =>
            <a href={`#${sitem}`} className="article-content-list__subtitle">{sitem}</a>
          )]
          return items
        })}
      </div>
      </CollapsibleItem>
    </Collapsible>
  )
}
