import ArticleCard from "../ArticleCard/ArticleCard"

export function switchAttributeChips(list, value = {disabled: 'none', selected: 'none'}) {
    const chips = list
    chips.forEach(item => {
      if (value.disabled != 'none') {
        if (value.disabled) item.setAttribute('disabled', '')
        else item.removeAttribute('disabled')
      }
      if (value.selected != 'none') {
        if (value.selected) item.setAttribute('selected', '')
        else item.removeAttribute('selected')
      }
    })
  }

export function getSelectedTags(tag_list = []) {
    if (!tag_list) tag_list = []
    const groups_tags = tag_list.map(item => {
      return item.tag
    })
    const groups_chips = groups_tags.map(item => {
      return [...document.querySelectorAll('[group*=' + item + ']')]
    })
    const selected_chips =  groups_chips.map((item, index) => {
      const selected_items = []
      const elAllChips = document.querySelector('[value*=' + 'group-' + tag_list[index].tag + ']')
      if (elAllChips.hasAttribute('selected')) return 'all'
        item.forEach(chip => {
          if (chip.hasAttribute('selected'))
            selected_items.push(chip)
        })
      return selected_items
    })
    return selected_chips
  }

  export function generateFilterURL(selected_chips, tag_list = []) {
    if (!tag_list) tag_list = []
    let filter = '' 
    let counter_selected_groups = 0
    selected_chips.forEach((item, index) => {
      let selected_items = ''
      if (item == 'all') {
        selected_items = 'all'
        counter_selected_groups++
      } else {
        item.forEach((item) => {
          selected_items += item.getAttribute('value') + '+'
        })
        selected_items = selected_items.slice(0, -1)
      }
      filter += tag_list[index].tag + '=' + '[' + selected_items + ']' + '&'
    })
    if (counter_selected_groups == tag_list.length) filter = 'all'
    else filter = filter.slice(0, -1)
    
    return filter
  }

  export function handleChipClick(el) {
    const elem = el.target
    const selected = updateSelectedChip(elem)
    const value = elem.getAttribute('value')
    let group_selector
    let group_tag
    let group_all_chip
    if (elem.hasAttribute('group')) {
      group_tag = elem.getAttribute('group')
      group_all_chip = document.querySelector('[value*=' + 'group-' + group_tag + ']')
      group_selector = 'group-' + group_tag + '_chips'
    }
    else group_selector = value + '_chips'
    const chips = [...document.getElementById(group_selector).querySelectorAll('.chip')]

    updateGroupsChips(value, elem, chips, function(chip) {
      if (selected) {
        chip.setAttribute('disabled', '')
      }
      else chip.removeAttribute('disabled')
    })
    if (group_all_chip && group_all_chip.hasAttribute('selected')) {
      group_all_chip.removeAttribute('selected')
      switchAttributeChips(chips, {disabled: false, selected: false})
      elem.setAttribute('selected', '')
    }
    if (!elem.hasAttribute('disabled')) updateGroupChips(group_tag, group_all_chip)
  }
  
  export function updateGroupChips(group_tag, group_all_chip) {
    if (!group_tag) return
    const chips = [...document.querySelectorAll('[group*=' + group_tag + ']')]
    let counter_selected = 0
    chips.forEach(item => {
      if (item.hasAttribute('selected'))
        counter_selected++
    })
    if (counter_selected == chips.length) {
      group_all_chip.setAttribute('selected', '')
      switchAttributeChips(chips, {disabled: true})
    }
  }

  export function generateArrayOfFilter(filter, tag_list) {
    if (!tag_list) tag_list = []
    let filterArray
    if (!(filter == 'all')) {
      filterArray = filter.split('&').map((item, index) => {
        item = item.slice(item.indexOf('[') + 1, item.indexOf(']')).split('+')
        if (item[0] == 'all') item = tag_list[index].tag + '_all'
        return item
      })
    } else
      filterArray = tag_list.map(item => (item.tag + '_all'))
    
    return filterArray
  }


  export function updateGroupsChips(value, elem, list, f) {
    if (value.includes('group')) {
      const chips = list
      for (let i = 0; i < chips.length; i++) {
        const chip = chips[i]
        if (chip == elem) continue
        f(chip)
      }
    }
  }

  export function updateSelectedChip(elem, value) {
    let selected
    if (elem.hasAttribute('selected') || (!value && value !== undefined)) {
      elem.removeAttribute('selected')
      selected = false
    } else {
      elem.setAttribute('selected', '')
      selected = true
    }
    return selected
  }
  
  export function getInfoArticles(articlesList, filter, tag_list) {
    const filterArray = generateArrayOfFilter(filter, tag_list)
    let filter_code = ''
    const filter_adv = filterArray.map((item, index) => {
      if (item == tag_list[index].tag + '_all') {
        filter_code += '1'
        return  tag_list[index].list.map(item => item.tag)
      } else if (item.length < 1 && item[0] == '') {
        filter_code += '0'
        return []
      } else {
        filter_code += '0'
        return item
      }
    })

    let ArticlesInfo = []
    // console.log(filter_adv[0], filter_adv[1], filter_adv[2])
    if (filter_code == '111') ArticlesInfo = articlesList.map(article => {
      return article
    })
    else {
      articlesList.forEach((article, index) => {
        let isView = true
        for (let i in filter_adv) {
          let isExist = false
          if (filter_adv[i] == '') continue
          for (let k in filter_adv[i]) {
            // console.log(article.tags, filter_adv[i][k])
            if (article.tags.includes(filter_adv[i][k])) {
              // isView = false
              // console.log("Includes", article.tags, filter_adv[i][k])
              isExist = true
              break
            }
          }

          if (isExist) continue
          else {
            isView = false
            break
          }
        }
        if (isView) 
          ArticlesInfo.push(article)
      })
    }

    return ArticlesInfo
  }
