import React, { useEffect } from 'react';
import { useHTTP } from '../../hooks/http.hook';

export default function ImagesLoad(props) {
  const { loading, error, request } = useHTTP()
  const setLoader = props.setLoader

  useEffect(() => {
    async function loadImagesData() {
      let data =  await request('http://95.163.236.18:80/api/getImages')
      localStorage.setItem('ImagesList', JSON.stringify(data))
      setLoader(true)  
    }
    loadImagesData()
  }, [])
  return <></>
}
