import React, { useState, useEffect } from 'react';
import { Modal, Button, Icon, Chip } from 'react-materialize';
import { useHTTP } from '../../hooks/http.hook';

export default function AdminContentModal(props) {
  const [loadStatus, setLoadStatus] = useState(false)
  const { loading, error, request } = useHTTP()

  const handlerConfirm = (props.type == 'req')
    ? (e) => {
      try {
        if (!props.handler(e, request)) return
        setLoadStatus('load')
        setTimeout(() => {
          if (!error) setLoadStatus('ok')
        }, 3000)
      } catch(e) {}
    } : (e) => props.handler(e)
  
  let btn_load_text = props.confirm_text || "Выполнить"
  if (loadStatus == 'load') btn_load_text = props.load_text || 'Выполняем...'
  if (loadStatus == 'ok') btn_load_text = <><Icon>check</Icon></>

  return (
        <Modal
        actions={[
          <Button flat modal="close" className='white-text' waves='teal' node="button">Закрыть</Button>,
          <Button flat className='white-text' type="submit" onClick={handlerConfirm} waves='teal' node="button" disabled={loadStatus}>{btn_load_text}</Button>
        ]}
        bottomSheet={false}
        header={props.header}
        id="Modal-media-files"
        open={false}
        options={{
          dismissible: true,
          endingTop: '10%',
          inDuration: 250,
          onCloseEnd: null,
          onCloseStart: null,
          onOpenEnd: null,
          onOpenStart: null,
          opacity: 0.5,
          outDuration: 250,
          preventScrolling: true,
          startingTop: '4%'
        }}
        trigger={props.trigger}
      >
        <div className="confirm_modal_body">{props.body || null}</div>
      </Modal>
  )
}
