import React from 'react';
import { useState } from 'react';
import { Button, TextInput, Preloader, ProgressBar} from 'react-materialize'
import { useNavigate } from 'react-router-dom';
import { useHTTP } from '../../hooks/http.hook';

export default function AdminAuth(props) {
  const { loading, request, error } = useHTTP()
  const [user, setUser] = useState({name: '', password: ''})
  const navigate = useNavigate()

  const handlerAuth = async (e) => {
    e.preventDefault()
    if (loading) return
    if (props.AdminAuth.isAdminAuth) return
    const data = await request('http://95.163.236.18:80/auth', 'POST', JSON.stringify(user), {'Content-Type': 'application/json'})
    if (data.accessToken) {
      props.AdminAuth.setIsAdminAuth(true)
      navigate('/admin/managment')
    }
  }

  return (
    <form className='admin-auth'>
      <h5 className='article-subtitle'>Авторизация</h5>
      <TextInput
        id="auth-admin-name"
        name='name'
        label="Имя"
        onChange={(e) => setUser({...user, name: e.target.value})}
      />
      <TextInput
        id="auth-admin-password"
        label="Пароль"
        name='password'
        password
        onChange={(e) => setUser({...user, password: e.target.value})}
      />
      <Button
        node="button"
        onClick={handlerAuth}
        waves="dark"
      >
        {loading ? <span className="preloader_auth-wrapper"><ProgressBar /></span>: 'Войти'}
        
      </Button>
    </form>
  )
}
