import React from 'react';
import { Button, Icon } from 'react-materialize';
import { Navigate, NavLink, useNavigate } from 'react-router-dom';
import { convertDateToString } from '../../convertDateString';
import { useHTTP } from '../../hooks/http.hook';
import AdminConfirmModal from '../AdminConfirmModal/AdminConfirmModal';

export default function AdminContentEdit(props) {
  const navigate = useNavigate()
  function editArticle() {
    let checked_list = localStorage.getItem('admin-content-list')
    if (checked_list) {
      checked_list = JSON.parse(checked_list)
      if (checked_list.length > 1 || checked_list.length == 0) {
        alert('Чтобы перейти в режим редактирования необходимо выбрать из только одну из статей.')
      } else {
        navigate('/admin/edit_article/' + checked_list[0])
      }
    }
  }

  return (
  <Button
    className="teal darken-3"
    fab={{
      direction: 'left',
      hoverEnabled: false
    }}
    floating
    large
    icon={<Icon>swap_horiz</Icon>}
    node="button"
  >
    <NavLink to='/admin/edit_article'>
      <Button
        className="green accent-4"
        floating
        icon={<Icon>note_add</Icon>}
        node="button"
      />
    </NavLink>
    <Button
      className="blue lighten-1"
      floating
      onClick={editArticle}
      icon={<Icon>edit</Icon>}
      node="button"
    />
    <AdminConfirmModal
      trigger={<Button
        className="red accent-2"
        floating
        icon={<Icon>delete_forever</Icon>}
        node="button"
      />}
      type="req"
      handler={deleteArticles}
      header="Удалить статьи?"
      confirm_text="Удалить"
      load_text="Удаляем..."
    />
  </Button>
  )
}

function handlerSaveArticle(request) {
  const article = JSON.parse(localStorage.getItem('article-edit'))
  article.views_count = 0
  article.date = convertDateToString(new Date())

  request('/api/addArticle', 'POST', JSON.stringify(article), {'Content-Type': 'application/json'})
}

function deleteArticles(e, request) {
  const ids = localStorage.getItem('admin-content-list')
  if (!ids || ids == '[]') {
    alert("Для начала нужно выбрать статьи!")
    return false
  }
  
  request('http://95.163.236.18:80/api/delArticle', 'POST', ids, {'Content-Type': 'application/json'})
  localStorage.setItem('admin-content-list', '[]')
  return true
}
