import React, { useState } from 'react';
import AdminContentEdit from '../AdminContentEdit/AdminContentEdit';
import ArticlesPage from '../../Pages/ArticlesPage/ArticlesPage';
import { Select } from 'react-materialize'
import AdminImagesImport from '../AdminImagesImport/AdminImagesImport'

export default function AdminManagment(props) {
  if (!props.isAdminAuth) {
    
  }

  const [content_type, setContentType] = useState('articles')
  function onChangeSelect(e) {
    const value = e.target.value
  console.log(content_type)

    setContentType(value)
  }
  let ArticlesList = JSON.parse(localStorage.getItem('ArticlesList'))
  let ContentPage 
  switch(content_type) {
    case 'articles':
      ContentPage = <ArticlesPage props={{isAdminAuth: props.isAdminAuth, ...props.props, ArticlesList}}></ArticlesPage>
      break
    case 'media':
      ContentPage = <AdminImagesImport/>
      break
    default:
      ContentPage = <ArticlesPage props={{isAdminAuth: props.isAdminAuth, ...props.props, ArticlesList}}></ArticlesPage>
      break
  }

  return (
    <div className='container'>
       <Select
        id="admin-content-select"
        multiple={false}
        onChange={onChangeSelect}
        options={{
          classes: '',
          dropdownOptions: {
            alignment: 'left',
            autoTrigger: true,
            closeOnClick: true,
            constrainWidth: true,
            coverTrigger: true,
            hover: false,
            inDuration: 150,
            onCloseEnd: null,
            onCloseStart: null,
            onOpenEnd: null,
            onOpenStart: null,
            outDuration: 250
          }
        }}
        value="articles"
      >
        <option value="articles">
          Показать статьи
        </option>
        <option value="media">
          Показать медиа
        </option>
      </Select>
      {ContentPage}
      <AdminContentEdit></AdminContentEdit>
    </div>
  )
}
