let UserLinks = [
  { name: 'Главная', href: '/' },
  { name: 'Общая информация', href: '/info' },
  { name: 'Обслуживание мотоцикла', href: '/service' },
]
let AdminLinks = [
  { name: 'Управление контентом', href: '/admin/managment' },
  { name: 'Статистика', href: '/admin/statistics' },
  { name: 'Сессия', href: '/admin/session' }
]
let NavLinks

function App() {
  const [isAdminAuth, setIsAdminAuth] = useState(false)
  setInterval(() => {
    if (isAdminAuth) {
      setIsAdminAuth(false)
    }
  }, 900000)

  if (isAdminAuth)
    NavLinks = [{ name: 'Главная', href: '/' }, ...AdminLinks]
  else
    NavLinks = UserLinks

  return (
    <BrowserRouter>
      <div className="App teal">
        <Header props={{SITE_NAME, LINKS: NavLinks}}></Header>
        <main style={{minHeight: isAdminAuth ? '100vh' : '71.1vh'}}>
          <BaseRouter articlesLinksList={articlesLinksList} AdminAuth={{isAdminAuth, setIsAdminAuth}} filtersTranslate={filtersTranslate}/>
        </main>
        {!isAdminAuth ? <Footer props={{SITE_NAME}}></Footer> : null }
      </div>
    </BrowserRouter>
  );
}

export default App;
