import React from 'react';
import { Routes, Route, Navigate, useNavigate } from 'react-router-dom';
import AdminManagment from '../../Admin/AdminManagment/AdminManagment';
import AdminArticleEditPage from '../../Admin/AdminArticleEditPage/AdminArticleEditPage';
import AdminAuth from '../../Admin/AdminAuth/AdminAuth';
import AdminSession from '../../Admin/AdminSession/AdminSession';
import AdminStatistics from '../../Admin/AdminStatistics/AdminStatistics';;



export default function AdminRouter(props) {
  const navigate = useNavigate()
  console.log(props.AdminAuth.isAdminAuth)
  if (!props.AdminAuth.isAdminAuth) {
    console.log('nav')
    navigate('/')
  }
  if (!props.articlesLinksList || !props.filtersTranslate) return (<p>Ошибка загрузки данных.</p>)
  const articlesLinksList = props.articlesLinksList 
  const filtersTranslate = props.filtersTranslate
  
  const ArticlesList = localStorage.getItem('ArticlesList')
  return (
    <Routes>
      {/* <Route exact path='/' element={<AdminAuth AdminAuth={props.AdminAuth}/>}/> */}
      <Route exact path='/managment/*' element={<AdminManagment isAdminAuth props={{articlesLinksList, filtersTranslate}}/>}/>
      <Route exact path='/statistics' element={<AdminStatistics isAdminAuth/>}/>
      <Route exact path='/session' element={<AdminSession isAdminAuth/>}/>
      <Route exact path='/edit_article' element={<AdminArticleEditPage isAdminAuth/>}/>
      <Route exact path='/edit_article/*' element={<AdminArticleEditPage isAdminAuth props={{isArticle: true, ArticlesList}}/>}/>
    </Routes>
  )
}
