import React from 'react';
import { Button, Icon, Modal } from 'react-materialize';
import AdminContentModal from '../AdminContentModal/AdminContentModal'
import image from '../../images';

export default function AdminImagesImport() {
  const imagesArr = []
  const images = image()
  for (let i in images) imagesArr.push(images[i])
  
  const videoArr = []
  
  return (
    <div>
       <div className="AdminArticleEditPage-header" style={{marginBottom: '10px'}}>
        <h5 className='article-subtitle white-text'>Изображения</h5>
        <AdminContentModal header='Импорт изображений' accept="image/*"></AdminContentModal>
      </div>
      <div className="AdminImagesImport">
        {imagesArr.length == 0 ? <p style={{color: '#e0e0e0'}}>Изображения отсутствуют</p> : ''}
        {imagesArr.map(item => {
          if (!item.includes('/static/media')) return null
          const key = item.split('/')[3].split('.')
          return (<div className="admin_mediainsp" data-name={key[0] + '.' + key[2]}><img width="650" src={item}/></div>)
        })}
      </div>
      <div className="AdminArticleEditPage-header" style={{marginBottom: '10px'}}>
        <h5 className='article-subtitle white-text'>Видеофайлы</h5>
        <AdminContentModal header='Импорт видеофайлов' accept="video/*"></AdminContentModal>
      </div>
      <div className="AdminImagesImport">
        {videoArr.length == 0 ? <p style={{color: '#e0e0e0'}}>Видеофайлы отсутствуют</p> : ''}
        {videoArr.map(item => <video width="650" src={item}/>)}
      </div>
    </div>
    
  )
}
