import React from 'react';
//import { Test } from './InfoPage.styles';
import ArticleRPage from '../../ArticleRPage/ArticleRPage';
import ScrollToTop from '../../ScrollToTop/ScrollToTop';

class InfoPage extends React.Component { 
  constructor(props) {
    super(props);

    this.state = {
      hasError: false,
    };
  }

  componentDidMount = () => {
    console.log('InfoPage mounted');
  }

  static getDerivedStateFromError(error) {
    // getDerivedStateFromError -> Update state so the next render will show the fallback UI.
    return { hasError: true };
  }

  componentDidCatch(error, info) {
    // You can also log the error to an error reporting service
  }

  getDerivedStateFromProps = (nextProps, prevState) => {
    console.log('InfoPage getDerivedStateFromProps', nextProps, prevState);
  }

  getSnapshotBeforeUpdate = (prevProps, prevState) => {
    console.log('InfoPage getSnapshotBeforeUpdate', prevProps, prevState);
  }

  componentDidUpdate = () => {
    console.log('InfoPage did update');
  }

  componentWillUnmount = () => {
    console.log('InfoPage will unmount');
  }

  render () {
    if (this.state.hasError) {
      return <h1>Something went wrongff.</h1>;
    }
    const ArticlesList = JSON.parse(localStorage.getItem('ArticlesList'))
    return (
      <div className="InfoPageWrapper white-text">
        <ArticleRPage props={{article_href: 'uralinfo', article_addition: {delBackButton: true}, ArticlesList: ArticlesList}}></ArticleRPage>
        <ScrollToTop/>
      </div>
    );
  }
}

InfoPage.propTypes = {
  // bla: PropTypes.string,
};

InfoPage.defaultProps = {
  // bla: 'test',
};

export default InfoPage;
