import React, { useState, useEffect } from 'react';
import { Modal, Button, Icon, Chip } from 'react-materialize';
import { useHTTP } from '../../hooks/http.hook';

export default function AdminContentModal(props) {

  const [files, setFiles] = useState([])
  const [loadStatus, setLoadStatus] = useState(false)

  function handlerChangeFiles(e) {
    const value = e.target.files
    setFiles(value)
  }

  const files_chips = []
  for (let i in files) {
    let name = files[i].name
    if (name && name != 'item') {
      if (name.length > 30)
        name = name.slice(0, 20) + '..' + name.split('.')[1]
      files_chips.push(<Chip 
        className='teal darken-2 white-text'
        style={{maxWidth: '90%'}}
        >{name}</Chip>)
    }
  }

  const { loading, error, request } = useHTTP()
  
  const handlerLoadContent = async () => {
    const form_data = new FormData()
    for (let i in files) {
      if (files[i].name && files[i].name != 'item') {
        form_data.append('file' + i, files[i], files[i].name)
      }
    }
    try {
      const data = request('/api/upload_images', 'POST', form_data)
      setLoadStatus('load')
      setTimeout(() => {
        if (!error) setLoadStatus('ok')
      }, 3000)
    } catch(e) {}
  }
  
  let btn_load_text = 'Загрузить'
  if (loadStatus == 'load') btn_load_text = 'Загружаем на сервер...'
  if (loadStatus == 'ok') btn_load_text = <><Icon>check</Icon></>

  return (
        <Modal
        actions={[
          <Button flat modal="close" className='white-text' waves='teal' node="button">Закрыть</Button>,
          <Button flat className='white-text' type="submit" onClick={handlerLoadContent} waves='teal' node="button" disabled={loadStatus}>{btn_load_text}</Button>
        ]}
        bottomSheet={false}
        header={props.header}
        id="Modal-media-files"
        open={false}
        options={{
          dismissible: true,
          endingTop: '10%',
          inDuration: 250,
          onCloseEnd: null,
          onCloseStart: null,
          onOpenEnd: null,
          onOpenStart: null,
          opacity: 0.5,
          outDuration: 250,
          preventScrolling: true,
          startingTop: '4%'
        }}
        trigger={<Button className='btn-floating btn-medium' onClick={function() {}}><Icon>add_circle_outline</Icon></Button>}
      >
        <div className="input-files-wrapper">
          
          {files_chips.length == 0 ?  <span>Выберите или перетащите файлы</span> : <p>{files_chips}</p>}
          <input id='input-images' name="files" accept={props.accept} multiple="multiple" type="file" onChange={handlerChangeFiles}/>
        </div>
      </Modal>
  )
}

async function fileToBlob(file, callback) {
  const fileReader = new FileReader();
  await fileReader.readAsArrayBuffer(file);
  fileReader.onload = async function () {
      callback(new Blob([this.result], {type: 'image/*'}));
  }
}