import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import './Navigation.styles.css';
import { Navbar, NavItem, Tabs, Icon, Tab } from 'react-materialize';
import { Link, Router, NavLink } from 'react-router-dom';
import logo from '../images/logo1 (1).svg'


const NavLinks = [
  { title: 'Главная', href: '/appointments' },
  { title: 'Общая информация', href: '/events'},
  { title: 'Обслуживание', href: '/notifications'},
  { title: 'Статьи', href: '/messages'},
]

class Navigation extends PureComponent { 
  constructor(props) {
    super(props);

    this.state = {
      hasError: false,
    };
  }

  componentDidMount = () => {
    console.log('Navigation mounted');
  }

  static getDerivedStateFromError(error) {
    // getDerivedStateFromError -> Update state so the next render will show the fallback UI.
    return { hasError: true };
  }

  componentDidCatch(error, info) {
    // You can also log the error to an error reporting service
  }

  getDerivedStateFromProps = (nextProps, prevState) => {
    console.log('Navigation getDerivedStateFromProps', nextProps, prevState);
  }

  getSnapshotBeforeUpdate = (prevProps, prevState) => {
    console.log('Navigation getSnapshotBeforeUpdate', prevProps, prevState);
  }

  componentDidUpdate = () => {
    console.log('Navigation did update');
  }

  componentWillUnmount = () => {
    console.log('Navigation will unmount');
  }

  render () {
    if (this.state.hasError) {
      return <h1>Something went wrong.</h1>;
    }
    const props = this.props.props
    const SITE_NAME = props.SITE_NAME
    
    const LINKS = props.LINKS.map(item =>  {
      let style = ({ isActive }) => ({fontWeight: isActive ? 'bold' : 'normal', textDecoration: isActive ? 'underline 2px ' : 'none'})
      if (item.href == '/')
        style = () => {
          if (window.location.pathname == '/start')
            return {fontWeight: 'bold', textDecoration: 'underline 2px'}
          else
            return {fontWeight: 'normal', textDecoration: 'none'}
        }
        
      
      return <NavLink to={item.href} style={style}>
        <NavItem>{item.name}</NavItem>
      </NavLink>
    })
    return (
      <Navbar
        className="teal darken-3 nav"
        alignLinks="left"
        brand={
          <a className="brand-logo logo " href="#">
            <div className='brand-logo__text'>{SITE_NAME}</div>
              <img className="brand-logo__img" src={logo}></img>
          </a>
        }
          
        id="mobile-nav"
        menuIcon={<Icon>menu</Icon>}
        options={{
          draggable: true,
          edge: 'left',
          inDuration: 250,
          onCloseEnd: null,
          onCloseStart: null,
          onOpenEnd: null,
          onOpenStart: null,
          outDuration: 200,
          preventScrolling: true
        }}
      >
        {LINKS}
      </Navbar>
    );
  }
}

Navigation.propTypes = {
  // bla: PropTypes.string,
};

Navigation.defaultProps = {
  // bla: 'test',
};

