import React from 'react';
//import { Test } from './ArtcilesCollection.styles';
import { Collection, CollectionItem } from 'react-materialize';
import { Link } from 'react-router-dom';


class ArtcilesCollection extends React.Component { 
  constructor(props) {
    super(props);
    this.state = {
      hasError: false,
    };
  }

  componentDidMount = () => {
    console.log('ArtcilesCollection mounted');
  }

  static getDerivedStateFromError(error) {
    // getDerivedStateFromError -> Update state so the next render will show the fallback UI.
    return { hasError: true };
  }

  componentDidCatch(error, info) {
    // You can also log the error to an error reporting service
  }

  getDerivedStateFromProps = (nextProps, prevState) => {
    console.log('ArtcilesCollection getDerivedStateFromProps', nextProps, prevState);
  }

  getSnapshotBeforeUpdate = (prevProps, prevState) => {
    console.log('ArtcilesCollection getSnapshotBeforeUpdate', prevProps, prevState);
  }

  componentDidUpdate = () => {
    console.log('ArtcilesCollection did update');
  }

  componentWillUnmount = () => {
    console.log('ArtcilesCollection will unmount');
  }

  render () {
    if (this.state.hasError) {
      return <h1>Something went wrong.</h1>;
    }
    const props = this.props.props
    return (
      <div className="ArtcilesCollectionWrapper main-page-acoll">
        <Collection className='white-text coll1 teal darken-2' sizes='xl' header={<Link to={"/articles/" + props.filtersTranslate[props.item.name]} className="collection-item__header" >{props.item.name}</Link>}>
          {props.item.list.map((item, index) => 
            <CollectionItem className='teal darken-1'>
                <Link to={"/articles/" + props.filtersTranslate[props.item.name] + "/" + (props.index + 1) + "" + (index + 1)} >
                  <a href={item.href || ''} className="collection-item__link">
                    {item.name}
                  </a>
                </Link>
              </CollectionItem>
            
            )
          }
        </Collection> 
      </div>
    );
  }
}

ArtcilesCollection.propTypes = {
  // bla: PropTypes.string,
};

ArtcilesCollection.defaultProps = {
  // bla: 'test',
};

export default ArtcilesCollection;
