import React, { useState } from 'react';
import { Textarea, TextInput, Icon, Button, Checkbox } from 'react-materialize'
import AdminContentEdit from '../AdminContentEdit/AdminContentEdit'
import getArticleContentFromCode from '../ArticleCodeConverter';
import ArticleRPage from '../../ArticleRPage'
import { convertDateToString } from '../../convertDateString'
import { useHTTP } from '../../hooks/http.hook';
import { useEffect } from 'react';

export default function AdminArticleEditPage(props) {
  props = props.props
  let ArticlesList = localStorage.getItem('ArticlesList')
  if (!ArticlesList) console.log('err not has DATALOAD')
  else ArticlesList = JSON.parse(ArticlesList)

  let ArticleInfoSaved = JSON.parse(localStorage.getItem('article-edit'))
  let article_id
  if (props && props.isArticle && ArticleInfoSaved) {
    const id = window.location.pathname.split('/')[3]
    console.log(ArticleInfoSaved.href, id)
    if (ArticleInfoSaved.href != id) {
      ArticlesList.forEach(article => {
        if (article.href == id) {
          const key = article.img.split('/')[3].split('.')
          ArticleInfoSaved = {...article, img: key[0] + '.' + key[2]}
          localStorage.setItem('article-edit', JSON.stringify(article))
        }
      });
    } 
  } else {
    article_id = ArticlesList.length
  }

  const [isPreview, setIsPreview] = useState(false)
  const [ArticleInfo, setArticleInfo] = useState(ArticleInfoSaved || {name: 'Мотоцикл Урал', tags: 'Теги', href: article_id, body: '', views_count: 0, num_titles: false})
  const [numerationTitles, setNumerationTitles] = useState(false)

  const handlerChangeName = (e) => { setArticleInfo({...ArticleInfo, name: e.target.value}) }
  const handlerChangeTags = (e) => { setArticleInfo({...ArticleInfo, tags: e.target.value}) }
  const handlerChangeBody = (e) => { setArticleInfo({...ArticleInfo, body: e.target.value}) }
  const handlerChangeKeyWords = (e) => { setArticleInfo({...ArticleInfo, key_words: e.target.value }) }
  const handlerChangePreText = (e) => { setArticleInfo({...ArticleInfo, pre_text: e.target.value }) }
  const handlerChangeHref = (e) => { setArticleInfo({...ArticleInfo, href: e.target.value }) }
  const handlerChangeImg = (e) => { setArticleInfo({...ArticleInfo, img: e.target.value }) }
  const switchNumerationTitles = (e) => { setArticleInfo({...ArticleInfo, num_titles: !ArticleInfo.num_titles }) }
  
  const [loadStatus, setLoadStatus] = useState(false)
  const { loading, error, request } = useHTTP()
  
  async function handlerSaveArticle() {
    const article = JSON.parse(localStorage.getItem('article-edit'))
    article.views_count = 0
    article.date = convertDateToString(new Date())
    try {
      if (props && props.isArticle) {
        request('http://95.163.236.18:80/api/updateArticle', 'POST', JSON.stringify(article), {'Content-Type': 'application/json'})
      } else {
        const data = request('http://95.163.236.18:80/api/addArticle', 'POST', JSON.stringify(article), {'Content-Type': 'application/json'})
      }
        // await request('http://95.163.236.18:80/api/delArticle', 'POST', JSON.stringify([ArticleInfo.href]), {'Content-Type': 'application/json'})

      setLoadStatus('load')
      setTimeout(() => {
        if (!error) {
          setLoadStatus('ok')
          setTimeout(() => {
            setLoadStatus(false)
          }, 6000)
          setTimeout(() => {
            localStorage.setItem('article-edit', null)
            setArticleInfo({ name: 'Мотоцикл Урал', tags: 'Теги', href: ++article_id, body: '',
              img: '', pre_text: '', key_words: '' })
          }, 6000)
        }
      }, 3000)
    } catch(e) {}
  }
  console.log(ArticleInfo)
  let article = { ...ArticleInfo, date: convertDateToString(new Date())}

  localStorage.setItem('article-edit', JSON.stringify(ArticleInfo))

  let btn_load_text = 'Сохранить'
  if (loadStatus == 'load') btn_load_text = 'Сохраняем на сервер...'
  if (loadStatus == 'ok') btn_load_text = <><Icon>check</Icon></>
  return (
    <div className="AdminArticleEditPage container">
      <div className="AdminArticleEditPage-header">
        <h5 className='article-subtitle white-text'>Редактор статей | {!isPreview ? 'Редактирование' : 'Предосмотр'}</h5>
        <Button flat className='white-text' onClick={function() {setIsPreview(!isPreview)}}><Icon>compare_arrows</Icon></Button>
        <Button flat className='white-text' style={{marginLeft: 40}} type="submit" onClick={handlerSaveArticle} waves='teal' node="button" disabled={loadStatus}>{btn_load_text}</Button>
      </div>
      {!isPreview ? 
        <div className='article-edit-page'>
          <AdminContentEdit></AdminContentEdit>
          <div className="article-edit-page__info">
            <TextInput
              id="article-edit-page__input-name"
              label="Название *"
              value={ArticleInfo.name}
              onChange={handlerChangeName}
            />
            <TextInput
              id="article-edit-page__input-img"
              label="Превью изображение *"
              value={ArticleInfo.img}
              onChange={handlerChangeImg}
            />
            <TextInput
              id="article-edit-page__input-href"
              label="Кастом ссылка"
              value={ArticleInfo.href}
              onChange={handlerChangeHref}
            />
            <TextInput
              id="article-edit-page__input-tags"
              label="Теги *"
              value={ArticleInfo.tags}
              onChange={handlerChangeTags}
            />
            <TextInput
              id="article-edit-page__input-key_words"
              label="Ключевые слова *"
              value={ArticleInfo.key_words}
              onChange={handlerChangeKeyWords}
            />
            <TextInput
              id="article-edit-page__input-pre_text"
              label="Превью текст *"
              value={ArticleInfo.pre_text}
              onChange={handlerChangePreText}
            />
            <div className="article-edit-page__checkbox">
              <Checkbox
                checked={ArticleInfo.num_titles}
                filledIn
                id="Checkbox_numeration-titles"
                label="Нумерация заголовков"
                onChange={switchNumerationTitles}
              />
            </div>
          </div>
          <div className="article-edit-page__body">
            <Textarea
              id="Textarea-article-edit"
              label="Содержание"
              l={12}
              m={12}
              s={12}
              xl={12}
              value={ArticleInfo.body}
              onChange={handlerChangeBody}
            />
          </div>
        </div> : 
        <div>
          <ArticleRPage props={{isAdminAuth: true, article}}></ArticleRPage>
        </div>}
    </div>
  )
}
