import React from 'react';
import {Routes, Route } from 'react-router-dom';
import InfoPage from '../../Pages/InfoPage/InfoPage';
import ServicePage from '../../Pages/ServicePage'
import HelloPage from '../../Pages/HelloPage'
import ArticleRPage from '../../ArticleRPage';
import AdminAuth from '../../Admin/AdminAuth/AdminAuth';
import AdminRouter from '../AdminRouter/AdminRouter';
import Loader from '../Loader/Loader';



export default function BaseRouter(props) {
  if (!props.articlesLinksList || !props.filtersTranslate) return (<p>Ошибка загрузки данных.</p>)
  const articlesLinksList = props.articlesLinksList 
  const filtersTranslate = props.filtersTranslate

  const ArticlesList = localStorage.getItem('ArticlesList')
  return (
    <Routes>
      <Route path='/info' element={<InfoPage />}/>
      <Route path='/service' element={<ServicePage />}/>
      <Route exact path='/article/:id' element={<ArticleRPage props={{articlesLinksList, filtersTranslate}}/>}/>
      <Route exact path='/auth' element={<AdminAuth AdminAuth={props.AdminAuth}/>}/>
      <Route exact path='/admin/*' element={props.AdminAuth.isAdminAuth ? <AdminRouter AdminAuth={props.AdminAuth} articlesLinksList={articlesLinksList} filtersTranslate={filtersTranslate}/> : <AdminAuth AdminAuth={props.AdminAuth}/>}/>
      <Route exact path='/start' element={<HelloPage props={{articlesLinksList, ArticlesList, filtersTranslate}}/>}/>
      <Route exact path='/start/*' element={<HelloPage props={{articlesLinksList, ArticlesList, filtersTranslate}}/>}/>
      <Route exact path='*' element={<Loader />}/>
    </Routes>
  )
}
