import React, { PureComponent, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
//import { Test } from './ArticleRPage.styles';
import { Icon, Button, Badge } from 'react-materialize';
import ScrollToTop from '../ScrollToTop/ScrollToTop';
import ArticleContentList from '../ArticleContentList/ArticleContentList'
import getArticleContentFromCode from '../Admin/ArticleCodeConverter';
import { useHTTP } from '../hooks/http.hook'
import Loader from '../Routers/Loader/Loader';

function ArticleRPage(props) {    
    const { loading, error, request } = useHTTP()
    const [article, setArticle] = useState({ href: false })
    const [loader, setLoader] = useState(false)

    useEffect(() => {
      async function updateViews() {
        if (loader && article.href) {
          await request('http://95.163.236.18:80/api/updateViewsArticle', 'POST', 
            JSON.stringify({...article, views_count: ++article.views_count }), {'Content-Type': 'application/json'})
        }
      }
      updateViews()
    }, [article, loader])

    if (!loader) return <Loader setLoader={setLoader}/>
    

    props = props.props
    let ArticlesList = props.ArticlesList
    const pathArray = window.location.pathname.split('/')
    let id = pathArray[pathArray.length - 1]
    let check_article
    if (props.article) check_article = props.article
    if (props.article_href) id = props.article_href
    // if (!props.titles) props.titles = []
    if (!ArticlesList) ArticlesList = JSON.parse(localStorage.getItem('ArticlesList'))
    if (!check_article) {
      for (let i in ArticlesList) {
        if (ArticlesList[i].href == id) {
          check_article = ArticlesList[i]
          break
        }
      }
    }
    
   
    
    if (props.article_addition) check_article = {...check_article, ...props.article_addition}
    if (!article.href) setArticle(check_article) 
    console.log('ARTICLE', article)
    const { titles, body } = getArticleContentFromCode(check_article.body, check_article.num_titles)
    
    if (!props.badges) props.badges = []

    const BackButton = (<Button large flat node="button" waves="default" className='back-button' onClick={() => window.history.back()}>
            <Icon className='white-text' large={true}>arrow_back</Icon>
          </Button>)
    return (
      <div className="ArticleRPageWrapper white-text">
      {!article.delBackButton ? BackButton : null}
       <div className="container">
       <div className='article-wrapper'>
          <div className="ArticleRPage-header">
            <div className="article-head">
              <h2 className="article-heading">{article.name}</h2>
            </div>
            <div className="article-info">
              <p>
                {props.badges.map(item =>  <span className="teal darken-1 white-text badge">{item}</span>)}
                
              <span className="teal darken-1 white-text badge">
                Дата публикации:&nbsp;&nbsp;&nbsp;{article.date}
              </span>
              <span className="teal darken-1 white-text badge">
                Просмотры:&nbsp;&nbsp;&nbsp;{article.views_count}
              </span>
              {article.isNew ? <span className="teal darken-1 white-text badge">Новое</span> : null}
              
              </p>
            </div>
          </div>
          
          <div className="article-body">
            <div className="article-content-list_wrapper">
              <ArticleContentList titles={titles} name={article.name}></ArticleContentList>
            </div>
            {body}
          </div>
      </div>
       </div>
      
      <ScrollToTop/>
      </div>
    );
}

ArticleRPage.propTypes = {
  // bla: PropTypes.string,
};

ArticleRPage.defaultProps = {
  // bla: 'test',
};

export default ArticleRPage;
