import React from 'react';
import { CollectionItem, Checkbox, Icon } from 'react-materialize';
import { NavLink } from 'react-router-dom';

export default function AdminArticleItem(props) {
  const article_info = props.article_info
  const current_value = localStorage.getItem('admin-content-list')
  let check_state 
  if (current_value) check_state = JSON.parse(current_value).includes(article_info.href)
  else check_state = false
  
  function handlerChangeList() {
    updateCheckedList(article_info.href, check_state = !check_state)
  }
  
  return (
    <CollectionItem className='teal admin-article-info-wrapper'>
      <NavLink to={'/article/' + article_info.href}> <a>{article_info.name}</a> </NavLink>
      <div className="secondary-content white-text admin-article-info">
          <div className='admin-article-info__item'>
            <Icon>access_time</Icon> 
            <span>{article_info.date}</span>
          </div>
          <div className='admin-article-info__item'>
            <Icon>remove_red_eye</Icon>
            <span>{article_info.views_count}</span>
          </div>
        <Checkbox filledIn checked={check_state} onChange={handlerChangeList}></Checkbox>
        </div>
  </CollectionItem>
  )
}

function updateCheckedList(id, checkState) {
  const prev_value = localStorage.getItem('admin-content-list')
  let list_values
  if (checkState) {
    if (!prev_value) list_values = [id]
    else {
      const prev_list = JSON.parse(prev_value)
      list_values = [...prev_list, id]
    }
  } else {
    if (!prev_value) return
    else list_values = JSON.parse(prev_value).filter(item => item != id)
  }
  console.log(list_values)
  localStorage.setItem('admin-content-list', JSON.stringify(list_values))
}
