import React from 'react';
//import { Test } from './ArticleCard.styles';
import {Card, Icon, Button, CardTitle, Chip, NavItem} from 'react-materialize'
import { NavLink } from 'react-router-dom';

class ArticleCard extends React.Component { 
  constructor(props) {
    super(props);

    this.state = {
      hasError: false,
    };
  }

  componentDidMount = () => {
    console.log('ArticleCard mounted');
  }

  static getDerivedStateFromError(error) {
    // getDerivedStateFromError -> Update state so the next render will show the fallback UI.
    return { hasError: true };
  }

  componentDidCatch(error, info) {
    // You can also log the error to an error reporting service
  }

  getDerivedStateFromProps = (nextProps, prevState) => {
    console.log('ArticleCard getDerivedStateFromProps', nextProps, prevState);
  }

  getSnapshotBeforeUpdate = (prevProps, prevState) => {
    // console.log('ArticleCard getSnapshotBeforeUpdate', prevProps, prevState);
  }

  componentDidUpdate = () => {
    console.log('ArticleCard did update');
  }

  componentWillUnmount = () => {
    console.log('ArticleCard will unmount');
  }

  render () {
    if (this.state.hasError) {
      return <h1>Something went wrong.</h1>;
    }
    const props = this.props.props
    return (
      <NavLink to={'/article/' + props.href}>
        <Card
            actions={[
              <Chip
                      close={false}
                      closeIcon={<Icon className="close">close</Icon>}
                      options={null}
                      className="teal lighten-4 date-chip"
                    >
                    <Icon className='edit'></Icon>
                    <span>{props.date}</span>
              </Chip>,
              props.isNew ? <Chip
                    close={false}
                    closeIcon={<Icon className="close">close</Icon>}
                    options={null}
                    className="green accent-2 date-chip"
                    style={{marginLeft: '5px', marginRight: '5px'}}
                  >
                  <Icon className='edit'></Icon>
                  <span>Новое</span>
                </Chip> : null,
              <Button flat node="button" waves='teal' className='white-text transparent btns'>Читать</Button>
            ]}
            closeIcon={<Icon>close</Icon>}
            header={<CardTitle image={props.img}>{props.name}</CardTitle>}
            revealIcon={<Icon>more_vert</Icon>}
            className="article-card teal darken-2"
        >
          <span className='white-text'>{props.pre_text}</span>
        </Card>
      </NavLink>
    );
  }
}

ArticleCard.propTypes = {
  // bla: PropTypes.string,
};

ArticleCard.defaultProps = {
  // bla: 'test',
};

export default ArticleCard;
