import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
//import { Test } from './ServicePage.styles';
import ArticleRPage from '../../ArticleRPage/ArticleRPage';
import ScrollToTop from '../../ScrollToTop/ScrollToTop';

class ServicePage extends React.Component { 
  constructor(props) {
    super(props);

    this.state = {
      hasError: false,
    };
  }

  componentDidMount = () => {
    console.log('ServicePage mounted');
  }

  static getDerivedStateFromError(error) {
    // getDerivedStateFromError -> Update state so the next render will show the fallback UI.
    return { hasError: true };
  }

  componentDidCatch(error, info) {
    // You can also log the error to an error reporting service
  }

  getDerivedStateFromProps = (nextProps, prevState) => {
    console.log('ServicePage getDerivedStateFromProps', nextProps, prevState);
  }

  getSnapshotBeforeUpdate = (prevProps, prevState) => {
    console.log('ServicePage getSnapshotBeforeUpdate', prevProps, prevState);
  }

  componentDidUpdate = () => {
    console.log('ServicePage did update');
  }

  componentWillUnmount = () => {
    console.log('ServicePage will unmount');
  }

  render () {
    if (this.state.hasError) {
      return <h1>Something went wrong.</h1>;
    }
    const ArticlesList = JSON.parse(localStorage.getItem('ArticlesList'))
    return (
      <div className="ServicePageWrapper">
        <ArticleRPage props={{article_href: 'serviceural', article_addition: {delBackButton: true}, ArticlesList}}></ArticleRPage>
        <ScrollToTop/>
      </div>
    );
  }
}

ServicePage.propTypes = {
  // bla: PropTypes.string,
};

ServicePage.defaultProps = {
  // bla: 'test',
};

export default ServicePage;
