import React, { useState } from 'react';
import DataLoad from './DataLoad';
import ImagesLoad from './ImagesLoad';
import { ProgressBar } from 'react-materialize'
import { useNavigate } from 'react-router-dom';
import image from '../../images';

export default function Loader(props) {
  // const [loader, setLoader] = useState({ ImagesLoader: false, DataLoader: false })
  const [imgLoader, setImgLoader] = useState(false)
  const [dataLoader, setDataLoader] = useState(false)

  const navigate = useNavigate()

  console.log(imgLoader, dataLoader)
  if (imgLoader && dataLoader) {
    const images = image()
    let data = dataLoader
    
    data = data.map(item => {
      const date = item.date.split('.')
      const date_obj = new Date(date[2], date[1] - 1, date[0])
      item.isNew = (new Date() - date_obj)/(1000*60*60) < 48

      return {...item, img: images[item.img], imgOld: item.img}
    })
    localStorage.setItem('ArticlesList', JSON.stringify(data))
    if (props.setLoader)
      props.setLoader(true)
    else
      navigate('/start')

  }

  return (
    <div className='load-data'>
      <DataLoad setLoader={setDataLoader}/>
      <ImagesLoad setLoader={setImgLoader}></ImagesLoad>
      <h3 className='article-subtitle white-text'>Загрузка...</h3>
      <ProgressBar />
    </div>
  )
}
