import React from 'react';
import SearchBar from '../../SearchBar/SearchBar';
import ArtcilesCollection from '../../ArtcilesCollection/ArtcilesCollection';
import ScrollToTop from '../../ScrollToTop/ScrollToTop';
import ArticlesPage from '../ArticlesPage/ArticlesPage';
import ArticlesSlider from '../../ArticlesSlider/ArticlesSlider'

class HelloPage extends React.Component { 
  constructor(props) {
    super(props);

    this.state = {
      hasError: false,
    };
  }
  
  componentDidMount = () => {
    console.log('HelloPage mounted');
  }

  static getDerivedStateFromError(error) {
    // getDerivedStateFromError -> Update state so the next render will show the fallback UI.
    return { hasError: true };
  }

  componentDidCatch(error, info) {
    // You can also log the error to an error reporting service
  }

  getDerivedStateFromProps = (nextProps, prevState) => {
    console.log('HelloPage getDerivedStateFromProps', nextProps, prevState);
  }

  getSnapshotBeforeUpdate = (prevProps, prevState) => {
    console.log('HelloPage getSnapshotBeforeUpdate', prevProps, prevState);
  }

  componentDidUpdate = () => {
    console.log('HelloPage did update');
  }

  componentWillUnmount = () => {
    console.log('HelloPage will unmount');
  }

  render () {
    if (this.state.hasError) {
      return <h1>Something went wrong.</h1>;
    }
    const props = this.props.props
    const {articlesLinksList, filtersTranslate} = props
    const ArticlesList = JSON.parse(localStorage.getItem('ArticlesList'))

    return (
      <div className="HelloPageWrapper white-text">
        <ArticlesSlider ArticlesList={ArticlesList}></ArticlesSlider>
        <SearchBar></SearchBar>
        {/* <div className="wrapper-collections">
          {props.articlesLinksList.map((item, index) => 
            <ArtcilesCollection props={{item, filtersTranslate: props.filtersTranslate, index}}></ArtcilesCollection>
          )} 
        </div> */}
        <div className="HelloPageWrapper_articles_wrapper container">
          <ArticlesPage props={{articlesLinksList, ArticlesList, filtersTranslate}}></ArticlesPage>
        </div>
        {/* <ScrollToTop/> */}
      </div>
    );
  }
}



HelloPage.propTypes = {
  // bla: PropTypes.string,
};

HelloPage.defaultProps = {
  // bla: 'test',
};

export default HelloPage;
