import React from 'react';
import { MediaBox } from 'react-materialize';
import { NavLink } from 'react-router-dom';
import images from '../../images';

function escapeRegExp(string) {
    return string.replace(/[.*+?^${}()|[\]\\]/g, '\\$&'); // $& means the whole matched string
  }

function replaceAll(str, find, replace) {
    return str.replace(new RegExp(escapeRegExp(find), 'g'), replace);
  }

let titles = ['']
let list_titles = []

const ArticleCode = [
    {key: '#text', code: (text) => <p className='article-text'>{text}</p>},
    {key: '#br', code: (text) => <br></br>},
    {key: '#title', code: (text) => <h3 class="article-subtitle"><a name={text}></a>{text}</h3>},
    {key: '#Stitle', code: (text) => <h4 class="article-subtitle" style={{marginLeft: 40}}><a name={text}></a>{text}</h4>},
    {key: '#media', code: function(options) {
        console.log(options.src)
        return <MediaBox options={{
            inDuration: 275,
            onCloseEnd: null,
            onCloseStart: null,
            onOpenEnd: null,
            onOpenStart: null,
            outDuration: 200
            }}
            style={{marginTop: '15px', width: options.width || '70%'}}
            className='article-media-box'>
                {options.img ? <img src={images()[options.img]} width='650'/> 
                 : (options.src ? <img src={options.src} width='650'/> : <img/>)}
            </MediaBox>
        }, isOptions: true   
    },
    {key: '#link', code: (options) => <a class="grey-text text-lighten-3 link" href={options.link}>{options.text}</a>, isOptions: true},
    {key: '#navlink', code: (options) => <NavLink to={options.to}>{options.innerHtml}</NavLink>, isOptions: true},
]
    
function convertArticleCodeToHtml(code, titles_numeration) {
    titles = ['']
    list_titles = []
    let body = code.split('\n').map(body_item => {
        return checkForCustomCode(body_item, titles_numeration)
    })
    console.log(list_titles)
    console.log(body)
    
    return body
}

function checkForCustomCode(str, titles_numeration) {
    let body_stroke
    ArticleCode.forEach(item => {
        if (str.slice(0, str.indexOf('[')).indexOf(item.key) != -1) {
            console.log('STROKE', str)
            let str_ = str.slice(str.indexOf('[') + 1, str.indexOf(']'))
            console.log(str_)
            if (item.isOptions) 
            {
                body_stroke = JSON.parse('{' + str_ + '}')
                console.log(body_stroke)
                if (body_stroke.innerHtml) {
                    body_stroke.innerHtml = replaceAll(body_stroke.innerHtml, '^', '"')
                    body_stroke.innerHtml = checkForCustomCode(body_stroke.innerHtml)
                }
                body_stroke = item.code(body_stroke)
                console.log(body_stroke)
            }
            else {
                if (item.key == '#title') {
                    titles.push(0)
                    titles[0] = titles.length - 1
                    if (titles_numeration)
                        str_ = (titles.length - 1) + '. ' + str_
                    list_titles.push([str_, []])
                } else if (item.key == "#Stitle") {
                    if (titles_numeration) {
                        const number = ++titles[titles[0]]
                        str_ = titles[0] + '.' + number + '. ' + str_
                    }
                    list_titles[(titles[0] - 1)][1].push(str_)
                }
                body_stroke = item.code(str_)
            }
        } else {
            // body_stroke = str
        }
    })
    if (!body_stroke) body_stroke = str
    console.log(titles)
    return body_stroke 
}


export default function getArticleContentFromCode(code, titles_numeration = false) {
    try {
        console.log("ARTICLEEEE ", convertArticleCodeToHtml(code, titles_numeration))
        return {
            titles: list_titles,
            body: <div className="article-inbody">{convertArticleCodeToHtml(code, titles_numeration)}</div>
        }
    } catch(e) {
        console.log(e.message)
    }
}

