import React, {useState, useMemo} from 'react';
//import { Test } from './ArticlesPage.styles';
import {Breadcrumb, Pagination, Icon, Collapsible, CollapsibleItem, Button, Chip, Select, Collection} from 'react-materialize'
import { NavLink, useNavigate } from 'react-router-dom';
import ArticleCard from '../../ArticleCard/ArticleCard';
import {handleChipClick, getInfoArticles, generateFilterURL, getSelectedTags} from '../../_Filter/index'
import AdminArticleItem from '../../Admin/AdminArticleItem'

const url = "http://localhost:3000/"
function ArticlesPage (props) { 
    props = props.props
    let navigate = useNavigate()
    const filtersTranslate = props.filtersTranslate
    let current_url = window.location.pathname
    let indexOfURL = current_url.indexOf('filter=') 
    current_url = current_url.slice(0, indexOfURL < 0 ? (current_url.length) : indexOfURL)
    const pathArray = window.location.pathname.slice(1).split('/')
    const localFilter = localStorage.getItem('articles_filter')
    let filter
    if (pathArray[1] && pathArray[1].includes('filter='))
      filter = pathArray[1].replace('filter=', '')
    else if (localFilter !== null)
      filter = localFilter
     else
      filter = 'all'
    let Links = []
    if (filter == 'all')
      Links.push(<NavLink to="/articles/all" >Фильтр</NavLink>)
    let ArticlesListInfo = useMemo(() => {
      return getInfoArticles(props.ArticlesList, filter, props.articlesLinksList)
    }, [filter, props.ArticlesList, props.articlesLinksList])
    console.log(ArticlesListInfo)
    let ArticleCards = ArticlesListInfo.map(item => <ArticleCard props={item}/>)
    let ArticleCollectionItems = ArticlesListInfo.map(item => <AdminArticleItem article_info={item}/>)
    let ArticlesItems = props.isAdminAuth ? ArticleCollectionItems : ArticleCards

    const [{activePage, countOnPage}, setPropsPage] = useState({activePage: 1, countOnPage:6}) 
    const searchCounter = ArticlesItems.length
    let pagination_count = (Math.ceil(searchCounter / countOnPage))
    pagination_count = pagination_count > 0 ? pagination_count : 1
    
    let start_index = (activePage - 1) * countOnPage
    let max_index = activePage * countOnPage
    if (activePage == pagination_count || pagination_count == 1) max_index = ArticlesItems.length
    const ArticleCardsOnPage = ArticlesItems.slice(start_index, max_index)

    const focusElem = document.getElementsByClassName('ArticlesPage-header')[0]
    function onSelectPage(number) {
      focusElem.focus()
      setPropsPage({activePage: number, countOnPage})
    }
    function handleClickCardsMore() {
      setPropsPage({activePage: 1, countOnPage: countOnPage + 6})
    }

    function handleChangePaginationSelect(e) {
      focusElem.focus()
      setPropsPage({activePage: 1, countOnPage: parseInt(e.target.value)})
    }

    const CollapsibleHeader = <div className='artcile-filter_header'>
      <div className='artcile-filter_header-title'>
        <Icon>find_replace</Icon>
        <p>Фильтр</p>
      </div>
      <Button className='btn-small btn-flat' onClick={
        function(el) {
          const filter = generateFilterURL(getSelectedTags(props.articlesLinksList), props.articlesLinksList)
          localStorage.setItem('articles_filter', filter)
          setPropsPage({activePage: 1, countOnPage})
          if (current_url == '/') current_url = '/articles'
          navigate(current_url + "/filter=" + filter)
        }
      }>Применить</Button>
    </div>

    const ArticlesContent = ArticlesItems.length > 0 ? ArticleCardsOnPage :
      <div className='article-filter_nothing-search'><p>К сожалению ничего не нашлось :(</p></div>
    return (
      <div className="ArticlesPageWrapper white-text">
          <div className="ArticlesPage-header" tabindex='3'>
          <Breadcrumb
            className="transparent articles-breadcrumb"
          >
            <NavLink to="/articles/all">Статьи</NavLink>
            {/* {Links} */}
          </Breadcrumb>
          
          <Collapsible accordion className='article-filter teal'>
              <CollapsibleItem
                expanded={false}
                header={CollapsibleHeader}
                node="div"
              >
              {/* <Chip className='article-filter_chip white' value={'all'} onClick={handleChipClick} selected="true">Все категории</Chip> */}
              {/* <Chip className='article-filter_chip white' value={'new'} onClick={handleChipClick} selected="true">Новое</Chip> */}
              {props.articlesLinksList.map((item) => {
                const name = item.name
                const chips = [<Chip className='article-filter_chip white' value={'group-' + item.tag} onClick={handleChipClick} selected="true">Все</Chip>]
                for (let i in item.list) {
                  chips.push( <Chip className='article-filter_chip white' group={item.tag} value={item.list[i].tag} onClick={handleChipClick} selected="true">{item.list[i].name}</Chip>)
                }
                return <div className='article-filter_item'>
                  <div className='article-filter_title'>
                    <Icon>{item.icon}</Icon>
                    <p>{name}</p>
                  </div>
                  <p className='article-filter_body' id={'group-' + item.tag + '_chips'}>
                    {chips}
                  </p>
                </div>
              })}
              </CollapsibleItem>
              
          </Collapsible>
          </div>

            {props.isAdminAuth ? <Collection style={{border: 'none'}}>{ArticlesContent}</Collection> : <div className="cards-wrapper">{ArticlesContent}</div>}

            {(pagination_count > 1 && activePage == 1) ? <a className='cards-more-button' onClick={handleClickCardsMore}>Показать больше</a> : null}
            <div className="ArticlesPage-footer">
              <Pagination
                activePage={activePage}
                items={pagination_count}
                leftBtn={<Icon id='pagination-left'>chevron_left</Icon>}
                maxButtons={10}
                rightBtn={<Icon id='pagination-right'>chevron_right</Icon>}
                className="artciles-pagination"
                onSelect={onSelectPage}
              />
          <div className='searched-info'>
            <span className='searched-item'>Найдено: {searchCounter}</span>
            <span className='searched-item'>Показано на странице: {(activePage == pagination_count && (searchCounter % countOnPage) != 0) ? (searchCounter % countOnPage) : countOnPage}</span>
            <span className='searched-item pagination-select'>Показывать на странице: &nbsp;
              <Select
                id="Paginaton-select-1"
                multiple={false}
                onChange={handleChangePaginationSelect}
                options={{
                  classes: '',
                  dropdownOptions: {
                    alignment: 'left',
                    autoTrigger: true,
                    closeOnClick: true,
                    constrainWidth: true,
                    coverTrigger: true,
                    hover: false,
                    inDuration: 150,
                    onCloseEnd: null,
                    onCloseStart: null,
                    onOpenEnd: null,
                    onOpenStart: null,
                    outDuration: 250
                  }
                  
                }}
              value={countOnPage}>
                <option value="6">6</option>
                <option value="9">9</option>
                <option value="15">15</option>
                <option value="20">20</option>
                <option value="25">25</option>
                <option value="35">35</option>
                <option value="50">50</option>
                <option value="75">75</option>
                <option value="100">100</option>
              </Select>
            </span>
          </div>
            </div>
          {/* <ScrollToTop/> */}
      </div>
    );
  }

ArticlesPage.propTypes = {
  // bla: PropTypes.string,
};

ArticlesPage.defaultProps = {
  // bla: 'test',
};

export default ArticlesPage;
